@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Raleway:wght@500&display=swap);
body{margin:0;padding:0;box-sizing:border-box}.App{display:flex;justify-content:center;align-items:center;width:100%;min-height:100vh}@media screen and (max-width: 700px){.App{align-items:flex-start}}.App .todo{width:600px}@media screen and (max-width: 700px){.App .todo{max-width:340px}}.App .todo h1{text-align:center;font-family:'Raleway', sans-serif;font-weight:700}.App .todo .slide-btn{display:flex;justify-content:space-evenly;margin-top:30px;width:100%;height:35px;border-bottom:1px solid #BDBDBD;font-family:'Montserrat', sans-serif}.App .todo .slide-btn button{width:95px;border:none;height:100%;cursor:pointer;background:none;font-size:14px;outline:none;letter-spacing:1.5px}.App .todo .slide-btn .slide-btn-active{border-bottom:2px solid #2F80ED}.App .todo .input-item{margin-top:20px;width:100%;display:flex;justify-content:space-between}.App .todo .input-item input[type~="text"]{width:80%;height:50px;margin-right:10px;border:1px solid #BDBDBD;box-sizing:border-box;border-radius:12px;font-size:16px;padding:0 15px;outline:none}.App .todo .input-item input[type~="text"]:focus{border:2px solid #BDBDBD}.App .todo .input-item button{width:20%;height:50px;background:#2F80ED;border-radius:12px;font-size:14px;border:none;cursor:pointer;color:#fff;outline:none}.App .todo .input-item button:hover{box-shadow:0 0 3px 1px rgba(130,130,130,0.4)}.App .todo .list-items{width:100%;margin-top:30px}.App .todo .list-items ul{margin:0;padding:0;list-style:none}.App .todo .list-items ul li{position:relative}.App .todo .list-items ul li label{font-size:18px;display:block;position:relative;padding-left:35px;cursor:pointer;font-family:'Montserrat', sans-serif}.App .todo .list-items ul li label:hover input ~ .checkmark{background-color:#ccc}.App .todo .list-items ul li label input{position:absolute;opacity:0;width:0;height:0;cursor:pointer}.App .todo .list-items ul li label input:checked ~ span{background-color:#2F80ED}.App .todo .list-items ul li label input:checked ~ span::after{display:block}.App .todo .list-items ul li label span{width:24px;height:24px;position:absolute;border-radius:5px;top:-1px;left:0;background:#eee}.App .todo .list-items ul li label span::after{position:absolute;content:"";display:none;top:4px;left:8px;height:10px;width:5px;border:solid #fff;border-width:0 3px 3px 0;transform:rotate(45deg)}.App .todo .list-items ul li label p{padding-right:50px}.App .todo .list-items ul li label .text-checked{text-decoration:line-through}.App .todo .list-items ul li .btn-del-all{width:100px;height:30px;font-size:15px;margin-top:20px;border:none;cursor:pointer;color:#fff;background:#EB5757;position:absolute;right:0;top:0;border-radius:4px}.App .todo .list-items ul li .button-del{position:absolute;display:inline-block;right:0;top:0;width:40px;border:none;background:#EB5757;height:23px;cursor:pointer;color:#fff;border-radius:4px}.App .todo .list-items ul li .button-del-none{display:none}

